.navbarContainer {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #232d63;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
}

.navLogo {
  font-weight: 700;
  font-size: 24px;
  margin: 15px;
  text-transform: uppercase;
  color: red;
}

.navItems > a {
  color: #ffffff;
  font-size: 18px;
  text-decoration: none;
  margin: 15px;
  position: relative;
  opacity: 0.9;
}

.navItems > a:hover {
  opacity: 1;
}

.navToggle {
  display: none;
}

@media screen and (max-width: 768px) {
  .navItems {
    position: absolute;
    top: 60px;
    display: flex;
    flex-direction: column;
    background: #0f1329;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: translateX(-100%);
    transition: all 0.45s;
  }

  .navItems > a {
    font-size: 20px;
  }

  .navItems > a::before {
    background: transparent;
  }

  .navItems.open {
    transform: translateX(0);
  }

  .navToggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .navToggleBar {
    position: relative;
    width: 32px;
    height: 2px;
    background: #ffffff;
    transition: all 0.45s ease-in-out;
  }

  .navToggleBar::before,
  .navToggleBar::after {
    content: "";
    position: absolute;
    height: 2px;
    background: #ffffff;
    border-radius: 2px;
    transition: all 0.45s ease-in-out;
  }

  .navToggleBar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
  }

  .navToggleBar::after {
    width: 32px;
    transform: translateY(8px);
  }

  .navToggle.open > .navToggleBar {
    transform: translateX(-40px);
    background: transparent;
  }

  .navToggle.open > .navToggleBar::before {
    width: 32px;
    transform: rotate(45deg) translate(26px, -26px);
  }

  .navToggle.open > .navToggleBar::after {
    transform: rotate(-45deg) translate(26px, 26px);
  }
}
